'use client';

import { useState, useEffect, useRef, useCallback } from 'react';
import { videoApi, parseSeasonEpisodeFromUrl } from '@/lib/api/video-api';
import type { VideoSource } from '@/lib/api/video-api';
import { useRouter, useSearchParams } from 'next/navigation';
import { ArrowLeft } from 'lucide-react';

interface VideoPlayerProps {
  mediaId: number;
  mediaType: 'movie' | 'tv';
  title?: string;
  season?: number;
  episode?: number;
  onClose?: () => void;
  loadTimeoutMs?: number;
  showControls?: boolean;
}

export default function VideoPlayer({
  mediaId,
  mediaType,
  title,
  season,
  episode,
  onClose,
  loadTimeoutMs = 7000,
  showControls = true
}: VideoPlayerProps) {
  const [embedUrl, setEmbedUrl] = useState<string>('');
  const [candidateUrls, setCandidateUrls] = useState<string[]>([]);
  const [sources, setSources] = useState<VideoSource[]>([]);
  const [currentSourceIndex, setCurrentSourceIndex] = useState(0);
  const [error, setError] = useState<string | null>(null);
  const [iframeKey, setIframeKey] = useState(0);

  const [currentSeason, setCurrentSeason] = useState<number | undefined>(season);
  const [currentEpisode, setCurrentEpisode] = useState<number | undefined>(episode);
  const [showTitle, setShowTitle] = useState<string>(title || '');

  const iframeRef = useRef<HTMLIFrameElement | null>(null);
  const attemptIdRef = useRef(0); // increments for each tryIndex call
  const timeoutHandleRef = useRef<number | null>(null);
  const failedSetRef = useRef<Set<number>>(new Set());
  const exhaustedRef = useRef(false);

  const router = useRouter();
  const searchParams = useSearchParams();

  // parse season/episode from URL params if tv
  useEffect(() => {
    if (mediaType === 'tv') {
      const urlSeason = searchParams.get('season');
      const urlEpisode = searchParams.get('episode');
      if (urlSeason && urlEpisode) {
        setCurrentSeason(parseInt(urlSeason));
        setCurrentEpisode(parseInt(urlEpisode));
      }
    }
  }, [searchParams, mediaType]);

  // auto-next episode (approx runtime fallback)
  useEffect(() => {
    if (mediaType !== 'tv' || !currentEpisode || !currentSeason) return;
    const runtimeMs = 25 * 60 * 1000; // 25 minutes fallback
    const timer = setTimeout(() => {
      const nextEpisode = (currentEpisode || 1) + 1;
      router.replace(`/watch/tv/${mediaId}?season=${currentSeason}&episode=${nextEpisode}`, { scroll: false });
      setCurrentEpisode(nextEpisode);
      setIframeKey(k => k + 1);
    }, runtimeMs);

    return () => clearTimeout(timer);
  }, [currentEpisode, currentSeason, mediaId, mediaType, router]);

  // clear current attempt timeout
  const clearAttemptTimeout = useCallback(() => {
    if (timeoutHandleRef.current !== null) {
      window.clearTimeout(timeoutHandleRef.current);
      timeoutHandleRef.current = null;
    }
  }, []);

  // pick next index that isn't failed yet; return -1 if none
  const pickNextIndex = useCallback((): number => {
    const len = candidateUrls.length;
    if (len === 0) return -1;
    for (let i = 1; i <= len; i++) {
      const idx = (currentSourceIndex + i) % len;
      if (!failedSetRef.current.has(idx)) return idx;
    }
    return -1;
  }, [candidateUrls.length, currentSourceIndex]);

  // start attempt timeout for an index; uses attemptIdRef to avoid stale timeouts acting
  const startAttempt = useCallback((idx: number) => {
    clearAttemptTimeout();
    attemptIdRef.current += 1;
    const thisAttemptId = attemptIdRef.current;

    timeoutHandleRef.current = window.setTimeout(() => {
      // if attempt id still matches, this attempt timed out
      if (attemptIdRef.current === thisAttemptId) {
        console.warn(`Attempt ${thisAttemptId} timed out for index ${idx}`);
        failedSetRef.current.add(idx);
        const next = pickNextIndex();
        if (next === -1) {
          exhaustedRef.current = true;
          setError('All video sources failed to load');
          clearAttemptTimeout();
        } else {
          tryIndex(next);
        }
      }
    }, loadTimeoutMs) as unknown as number;
  }, [clearAttemptTimeout, loadTimeoutMs, pickNextIndex]);

  // attempt to switch to index: sets embedUrl and starts attempt
  const tryIndex = useCallback((idx: number) => {
    if (!candidateUrls || candidateUrls.length === 0) {
      setError('No video sources available');
      return false;
    }
    if (idx < 0 || idx >= candidateUrls.length) return false;

    // if idx already failed but others exist, pick next available
    if (failedSetRef.current.has(idx) && failedSetRef.current.size < candidateUrls.length) {
      const alt = pickNextIndex();
      if (alt === -1) {
        exhaustedRef.current = true;
        setError('All video sources failed to load');
        return false;
      }
      idx = alt;
    }

    const url = candidateUrls[idx];
    console.log(`Attempting source ${idx + 1}/${candidateUrls.length}: ${url}`);
    setCurrentSourceIndex(idx);
    setEmbedUrl(url);
    setIframeKey(k => k + 1);
    setError(null);
    startAttempt(idx);
    return true;
  }, [candidateUrls, pickNextIndex, startAttempt]);

  // try next available
  const tryNext = useCallback(() => {
    if (!candidateUrls || candidateUrls.length === 0) {
      setError('No video sources available');
      return false;
    }
    if (exhaustedRef.current) {
      setError('All video sources failed to load');
      return false;
    }
    const next = pickNextIndex();
    if (next === -1) {
      exhaustedRef.current = true;
      setError('All video sources failed to load');
      clearAttemptTimeout();
      return false;
    }
    return tryIndex(next);
  }, [candidateUrls, pickNextIndex, tryIndex, clearAttemptTimeout]);

  // try previous (manual): allow overriding failedSet
  const tryPrev = useCallback(() => {
    if (!candidateUrls || candidateUrls.length === 0) return;
    const prev = (currentSourceIndex - 1 + candidateUrls.length) % candidateUrls.length;
    failedSetRef.current.delete(prev);
    tryIndex(prev);
  }, [candidateUrls, currentSourceIndex, tryIndex]);

  // iframe events
  const handleIframeLoad = useCallback(() => {
    console.log(`Iframe onLoad for index ${currentSourceIndex}`);
    clearAttemptTimeout();
    attemptIdRef.current = 0;
    failedSetRef.current.delete(currentSourceIndex);
    exhaustedRef.current = false;
    setError(null);
  }, [currentSourceIndex, clearAttemptTimeout]);

  const handleIframeError = useCallback(() => {
    console.warn(`Iframe onError for index ${currentSourceIndex}`);
    clearAttemptTimeout();
    attemptIdRef.current = 0;
    failedSetRef.current.add(currentSourceIndex);
    const next = pickNextIndex();
    if (next === -1) {
      exhaustedRef.current = true;
      setError('All video sources failed to load');
    } else {
      tryIndex(next);
    }
  }, [currentSourceIndex, clearAttemptTimeout, pickNextIndex, tryIndex]);

  // load candidates from server, fallback to videoApi
  useEffect(() => {
    let cancelled = false;

    const loadCandidates = async () => {
      setCandidateUrls([]);
      setSources([]);
      setEmbedUrl('');
      setCurrentSourceIndex(0);
      exhaustedRef.current = false;
      failedSetRef.current.clear();
      setError(null);

      try {
        // try check-embed endpoint (returns list only)
        try {
          const res = await fetch('/api/check-embed', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              mediaType,
              tmdbId: mediaId,
              season: currentSeason || season || 1,
              episode: currentEpisode || episode || 1
            })
          });

          if (res.ok) {
            const data = await res.json();
            const list: string[] = [];

            if (data.workingUrl) {
              if (typeof data.workingUrl === 'string') list.push(data.workingUrl);
              else if (typeof data.workingUrl === 'object' && data.workingUrl.url) list.push(data.workingUrl.url);
            }

            if (Array.isArray(data.urls) && data.urls.length > 0) {
              for (const u of data.urls) {
                if (u && !list.includes(u)) list.push(u);
              }
            }

            const final = list.filter(Boolean);
            if (final.length > 0) {
              if (cancelled) return;
              setCandidateUrls(final);
              // immediately start first attempt
              tryIndex(0);
              return;
            }
          } else {
            console.warn('check-embed non-OK, falling back');
          }
        } catch (err) {
          console.warn('check-embed failed, falling back', err);
        }

        // fallback to videoApi
        const fallback = mediaType === 'movie'
          ? await videoApi.getMovieSources(mediaId)
          : await videoApi.getTVSources(mediaId, currentSeason || season || 1, currentEpisode || episode || 1);

        if (cancelled) return;
        setSources(fallback || []);
        const fallbackUrls = (fallback || []).map(s => s.url).filter(Boolean);
        if (fallbackUrls.length === 0) {
          setError('No video sources available');
          return;
        }
        setCandidateUrls(fallbackUrls);
        tryIndex(0);
      } catch (err) {
        console.error('Failed to load video sources:', err);
        if (!cancelled) setError('Failed to load video sources');
      }
    };

    loadCandidates();

    return () => {
      cancelled = true;
      clearAttemptTimeout();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mediaId, mediaType, currentSeason, currentEpisode]);

  // UI close
  const handleClose = useCallback(() => {
    if (onClose) onClose();
    else router.push('/');
  }, [onClose, router]);

  // ESC to close
  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (e.key === 'Escape') handleClose();
    };
    document.addEventListener('keydown', onKey);
    return () => document.removeEventListener('keydown', onKey);
  }, [handleClose]);

  // cleanup timeout on unmount
  useEffect(() => {
    return () => clearAttemptTimeout();
  }, [clearAttemptTimeout]);

  return (
    <div className="fixed inset-0 z-50 bg-black">
      {/* Player */}
      {embedUrl && !error && (
        <>
          <iframe
            key={iframeKey}
            ref={iframeRef}
            src={embedUrl}
            className="w-full h-full"
            allowFullScreen
            allow="autoplay; encrypted-media; picture-in-picture"
            onLoad={handleIframeLoad}
            onError={handleIframeError}
            style={{ border: 'none' }}
            title={title || `player-${mediaType}-${mediaId}`}
          />

          {/* Back + Title */}
          <div className="absolute top-4 left-4 z-30 flex items-center gap-3">
            <button
              onClick={handleClose}
              className="p-2 rounded-full bg-black/60 text-white"
              aria-label="Back"
            >
              <ArrowLeft className="w-6 h-6" />
            </button>
            {showTitle && (
              <h1 className="text-white font-extrabold text-lg truncate max-w-xs">
                {showTitle}
              </h1>
            )}
          </div>

          {/* Season/Episode */}
          {mediaType === 'tv' && currentSeason && currentEpisode && (
            <div className="absolute top-4 left-1/2 -translate-x-1/2 bg-black/60 px-4 py-1 rounded-md text-white">
              S{currentSeason}E{currentEpisode}
            </div>
          )}

          {/* Controls overlay */}
          <div className="absolute top-4 right-4 z-30 text-white/80 text-sm flex items-center gap-3">
            <div>Auto-switch enabled ‚Äî Press ESC to exit</div>
            <div className="text-xs ml-2">Source {currentSourceIndex + 1}/{candidateUrls.length || 0}</div>
            {showControls && candidateUrls.length > 0 && (
              <div className="ml-3 flex items-center gap-2">
                <button onClick={tryPrev} className="px-2 py-1 bg-black/60 rounded hover:bg-black/80">Prev</button>
                <button onClick={tryNext} className="px-2 py-1 bg-black/60 rounded hover:bg-black/80">Next</button>
              </div>
            )}
          </div>
        </>
      )}

      {/* Error */}
      {error && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-white text-center max-w-md px-6">
            <div className="text-6xl mb-6">‚ö†Ô∏è</div>
            <h2 className="text-2xl font-bold mb-4">Unable to Play Video</h2>
            <p className="text-gray-300 mb-4">{error}</p>
            <div className="flex items-center justify-center gap-3">
              <button onClick={handleClose} className="px-6 py-2 bg-gray-700 rounded">Go Back</button>
              {candidateUrls.length > 0 && <button onClick={() => tryIndex(0)} className="px-6 py-2 bg-gray-600 rounded">Retry</button>}
            </div>
          </div>
        </div>
      )}

      {/* Nothing */}
      {!embedUrl && !error && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="text-white text-center">
            <div className="text-4xl mb-4">üì∫</div>
            <h2 className="text-xl font-bold mb-2">No Video Sources Available</h2>
            <p className="text-gray-400 mb-6">Unable to find any playable video sources for this content</p>
            <button onClick={handleClose} className="px-8 py-3 bg-gray-700 hover:bg-gray-600 rounded-lg">Go Back</button>
          </div>
        </div>
      )}
    </div>
  );
}
