// lib/api/video-api.ts
import axios from "axios";

export interface VideoSource {
  url: string;
  quality: string;
  type: "iframe";
  servers: string;
  tmdbId: number;
  mediaType: "movie" | "tv";
  season?: number;
  episode?: number;
  title?: string;
  episodeTitle?: string;
}

/** Parse season/episode from various URL formats */
export function parseSeasonEpisodeFromUrl(url: string): { season: number | null; episode: number | null } {
  try {
    const parsed = new URL(url);
    const pathSegments = parsed.pathname.split('/').filter(Boolean);

    // Try path-based: /tv/{id}/{season}/{episode}
    if (pathSegments.length >= 4 && pathSegments[0] === 'tv') {
      const season = parseInt(pathSegments[2], 10);
      const episode = parseInt(pathSegments[3], 10);

      if (!isNaN(season) && !isNaN(episode) && season > 0 && episode > 0) {
        return { season, episode };
      }
    }

    // Try query params: ?s=1&e=2 or ?season=1&episode=2
    const seasonParam = parsed.searchParams.get('s') || parsed.searchParams.get('season');
    const episodeParam = parsed.searchParams.get('e') || parsed.searchParams.get('episode');

    if (seasonParam && episodeParam) {
      const season = parseInt(seasonParam, 10);
      const episode = parseInt(episodeParam, 10);

      if (!isNaN(season) && !isNaN(episode) && season > 0 && episode > 0) {
        return { season, episode };
      }
    }

    return { season: null, episode: null };
  } catch (error) {
    console.warn('Failed to parse season/episode from URL:', url, error);
    return { season: null, episode: null };
  }
}

/** Read and normalize environment variables */
const ENV = {
  BASE: (process.env.NEXT_PUBLIC_VIDEO_API_BASE || "").trim(),
  E1: (process.env.NEXT_PUBLIC_VIDEO_EMBED_1 || "https://111movies.com/${type}/${id}/${season}/${episode}?autoplay=1&theme=23ddc36c&autoNext=1").trim(),
  E2: (process.env.NEXT_PUBLIC_VIDEO_EMBED_2 || "https://111movies.com/${type}/${id}?autoplay=1&theme=23ddc36c&autoNext=1").trim(),
  E3: (process.env.NEXT_PUBLIC_VIDEO_EMBED_3 || "").trim(),
  E4: (process.env.NEXT_PUBLIC_VIDEO_EMBED_4 || "").trim(),
};

/** Global extra params appended to every embed URL */
const EXTRA_PARAMS = (process.env.NEXT_PUBLIC_VIDEO_URL_PARAMS || "").trim();

/** Providers list with friendly names (fallback order) */
const VIDEO_PROVIDERS = [
  { name: "Primary", value: ENV.E1, priority: 1 },
  { name: "Secondary", value: ENV.E2, priority: 2 },
  { name: "Tertiary", value: ENV.E3, priority: 3 },
  { name: "Backup", value: ENV.E4, priority: 4 },
  { name: "Base", value: ENV.BASE, priority: 5 },
].filter(p => !!p.value);

/** Extract hostname for friendly server label */
function getServerLabel(url: string): string {
  try {
    const hostname = new URL(url).hostname;
    return hostname.replace(/^www\./, '');
  } catch {
    return 'Unknown Server';
  }
}

/** Build the default embed path for base URLs */
function buildEmbedPath(
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  if (mediaType === "movie") return `/movie/${tmdbId}`;
  if (season && episode) return `/tv/${tmdbId}/${season}/${episode}`;
  return `/tv/${tmdbId}`;
}

/** Check if provider uses template placeholders */
function isTemplate(url: string): boolean {
  return /\$\{(id|type|season|episode)\}/.test(url);
}

/** Normalize base URL (remove trailing slash) */
function normalizeBase(url: string): string {
  return url.endsWith("/") ? url.slice(0, -1) : url;
}

/** Append query parameters safely */
function appendParams(url: string, params: string): string {
  if (!params) return url;
  return url + (url.includes("?") ? "&" : "?") + params;
}

/** Build raw URL from template or base provider */
function buildRawUrl(
  providerValue: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  if (isTemplate(providerValue)) {
    return providerValue
      .replace(/\$\{id\}/g, String(tmdbId))
      .replace(/\$\{type\}/g, mediaType)
      .replace(/\$\{season\}/g, season ? String(season) : "1")
      .replace(/\$\{episode\}/g, episode ? String(episode) : "1");
  }

  const base = normalizeBase(providerValue);
  if (mediaType === "tv") {
    return `${base}/tv/${tmdbId}?s=${season || 1}&e=${episode || 1}`;
  }
  return `${base}/movie/${tmdbId}`;
}

/** Build final playable URL with extra params */
export function buildEmbedUrl(
  providerValue: string,
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string {
  const raw = buildRawUrl(providerValue, mediaType, tmdbId, season, episode);
  return appendParams(raw, EXTRA_PARAMS);
}

/** Get all embed URL candidates in priority order */
export function getEmbedUrlCandidates(
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number
): string[] {
  console.log(`üîç Getting embed URL candidates for ${mediaType} ${tmdbId}`, { season, episode });

  const urls = VIDEO_PROVIDERS.map(provider => {
    const url = buildEmbedUrl(provider.value, mediaType, tmdbId, season, episode);
    console.log(`üì∫ Generated URL for ${provider.name}: ${url}`);
    return url;
  });

  const uniqueUrls = [...new Set(urls)];
  console.log(`‚úÖ Total unique URLs generated: ${uniqueUrls.length}`);

  return uniqueUrls;
}

/** Create VideoSource with metadata from provider */
function createVideoSourceWithMetadata(
  provider: typeof VIDEO_PROVIDERS[0],
  mediaType: "movie" | "tv",
  tmdbId: number,
  season?: number,
  episode?: number,
  title?: string,
  episodeTitle?: string
): VideoSource {
  const url = buildEmbedUrl(provider.value, mediaType, tmdbId, season, episode);

  return {
    url,
    quality: "HD",
    type: "iframe",
    servers: `${provider.name} (${getServerLabel(url)})`,
    tmdbId,
    mediaType,
    season,
    episode,
    title,
    episodeTitle,
  };
}

/** Resolve TMDB ID from various input formats */
export async function resolveTmdbId(
  input: string | number,
  type: "movie" | "tv"
): Promise<number | null> {
  if (typeof input === "number" && Number.isFinite(input)) return input;

  const str = String(input).trim();
  const asNum = Number(str);
  if (Number.isFinite(asNum)) return asNum;

  // Handle IMDb IDs
  if (/^tt\d+$/i.test(str)) {
    const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
    if (!API_KEY) return null;

    try {
      const { data } = await axios.get(
        `https://api.themoviedb.org/3/find/${str}`,
        {
          params: { api_key: API_KEY, external_source: "imdb_id" },
          timeout: 5000
        }
      );

      const results = type === "movie" ? data.movie_results : data.tv_results;
      return results?.[0]?.id ?? null;
    } catch (error) {
      console.error('Failed to resolve IMDb ID:', error);
      return null;
    }
  }

  return null;
}

export const videoApi = {
  /** Check if any video APIs are configured */
  hasConfiguredApis(): boolean {
    return VIDEO_PROVIDERS.length > 0;
  },

  /** Get configuration status for debugging */
  getConfigStatus() {
    return {
      configured: VIDEO_PROVIDERS.length,
      providers: VIDEO_PROVIDERS.map(p => ({
        name: p.name,
        configured: !!p.value,
        isTemplate: isTemplate(p.value),
        priority: p.priority
      })),
      extraParams: EXTRA_PARAMS || 'none'
    };
  },

  /** Get all embed URL candidates for media - ATTACHED to videoApi object */
  getEmbedUrlCandidates(
    mediaType: "movie" | "tv",
    tmdbId: number,
    season?: number,
    episode?: number
  ): string[] {
    console.log(`üé¨ videoApi.getEmbedUrlCandidates called:`, { mediaType, tmdbId, season, episode });
    return getEmbedUrlCandidates(mediaType, tmdbId, season, episode);
  },

  /** Get movie video sources with metadata */
  async getMovieSources(tmdbId: number): Promise<VideoSource[]> {
    console.log(`üé¨ Getting movie sources for TMDB ID: ${tmdbId}`);
    const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
    let movieTitle = 'Unknown Movie';

    if (API_KEY) {
      try {
        const response = await axios.get(`https://api.themoviedb.org/3/movie/${tmdbId}`, {
          params: { api_key: API_KEY },
          timeout: 5000
        });
        movieTitle = response.data?.title || movieTitle;
        console.log(`üìΩÔ∏è Movie title: ${movieTitle}`);
      } catch (error) {
        console.warn('Failed to fetch movie title:', error);
      }
    }

    const sources = VIDEO_PROVIDERS.map(provider =>
      createVideoSourceWithMetadata(provider, "movie", tmdbId, undefined, undefined, movieTitle)
    );

    console.log(`‚úÖ Generated ${sources.length} movie sources`);
    return sources;
  },

  /** Get TV show video sources with metadata */
  async getTVSources(
    tmdbId: number,
    season?: number,
    episode?: number
  ): Promise<VideoSource[]> {
    console.log(`üì∫ Getting TV sources for TMDB ID: ${tmdbId}, S${season}E${episode}`);
    const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY?.trim();
    let showTitle = 'Unknown Show';
    let episodeTitle = `Episode ${episode || 1}`;

    if (API_KEY && season && episode) {
      try {
        const [tvResponse, episodeResponse] = await Promise.allSettled([
          axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}`, {
            params: { api_key: API_KEY },
            timeout: 5000
          }),
          axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}/season/${season}/episode/${episode}`, {
            params: { api_key: API_KEY },
            timeout: 5000
          })
        ]);

        if (tvResponse.status === 'fulfilled') {
          showTitle = tvResponse.value.data?.name || showTitle;
          console.log(`üì∫ Show title: ${showTitle}`);
        }

        if (episodeResponse.status === 'fulfilled') {
          episodeTitle = episodeResponse.value.data?.name || episodeTitle;
          console.log(`üé≠ Episode title: ${episodeTitle}`);
        }
      } catch (error) {
        console.warn('Failed to fetch TV metadata:', error);
      }
    } else if (API_KEY) {
      // Just fetch show title
      try {
        const response = await axios.get(`https://api.themoviedb.org/3/tv/${tmdbId}`, {
          params: { api_key: API_KEY },
          timeout: 5000
        });
        showTitle = response.data?.name || showTitle;
        console.log(`üì∫ Show title: ${showTitle}`);
      } catch (error) {
        console.warn('Failed to fetch show title:', error);
      }
    }

    const sources = VIDEO_PROVIDERS.map(provider =>
      createVideoSourceWithMetadata(
        provider,
        "tv",
        tmdbId,
        season,
        episode,
        showTitle,
        episodeTitle
      )
    );

    console.log(`‚úÖ Generated ${sources.length} TV sources`);
    return sources;
  },

  /** Debug helper to log all configured providers */
  logConfiguration() {
    console.log('üìä Video API Configuration:', this.getConfigStatus());
    console.log('üîß Available methods:', Object.keys(this));
    console.log('üîç getEmbedUrlCandidates available:', typeof this.getEmbedUrlCandidates === 'function');
  }
};
